<template>
  <el-scrollbar class="login-scroll">
    <el-row type="flex"
            class="row-bg mg-top100"
            justify="center"
            align="middle">
      <div class="login-wrap">
        <div class="login-title">用户登录</div>
        <el-form status-icon
                 :model="loginForm"
                 ref="loginForm"
                 label-width="60px">
          <el-form-item prop="user"
                        :rules="$validateRules({ required: true, requiredItem: '用户名' })">
            <el-input placeholder="请输入用户名"
                      v-model="loginForm.user"></el-input>
          </el-form-item>
          <el-form-item prop="password"
                        :rules="$validateRules({ required: true, requiredItem: '密码' })">
            <el-input type="password"
                      placeholder="请输入密码"
                      v-model="loginForm.password"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary"
                       @click="login('loginForm')">登录</el-button>
          </el-form-item>
          <el-form-item>
            <el-button @click="reset('loginForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-row>
  </el-scrollbar>
</template>

<script>
export default {
  components: {},
  props: {},
  data () {
    return {
      loginForm: {
        user: 'admin',
        password: 'admin'
      }
    }
  },
  watch: {},
  computed: {},
  methods: {
    login (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          sessionStorage.setItem('user', this.loginForm.user)
          sessionStorage.setItem('password', this.loginForm.password)
          this.$message({
            message: '登陆成功！',
            duration: 1200,
            type: 'success'
          })
          this.$router.push({
            path: '/register'
          })
        } else {
          console.error('error submit!!')
          return false
        }
      })
    },
    reset (formName) {
      this.$refs[formName].resetFields()
    }
  },
  created () { },
  mounted () { }
}
</script>
<style lang="">
.login-scroll {
  height: 100%;
}
.login-scroll .el-scrollbar__wrap {
  overflow-x: hidden;
}
.mg-top100 {
  margin-top: 10%;
}
.login-wrap {
  width: 350px;
  padding: 10px 30px;
  margin: 0 30px 30px;
  border-radius: 5px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}
.login-wrap .login-title {
  color: #606266;
  font-size: 20px;
  font-weight: 600;
  margin: 30px auto;
  text-align: center;
}
.login-wrap .el-form-item__content {
  margin-left: 0 !important;
}
.login-wrap .el-button {
  width: 100%;
}
</style>



